@import '../../../scss/vars.scss';

.list {
    list-style: none;
    margin: 0;
    padding: 0;
    margin-bottom: 19px;
}

.item {
    border-top: 1px solid $grayColorE4;

    &:last-child {
        border-bottom: 1px solid $grayColorE4;
    }
}

.time {
    margin: 0;
    color: $grayColor99;
    font-weight: 300;
    font-size: 16px;
    line-height: 17px;
}